$primary: #A49169;

#room {

    .text-wrapper {
        margin-bottom: 1rem;
        text-align: center;
        font-size: 1.2rem;
    }

    .cta-wrapper {
        text-align: center;

        a {
            display: inline-block;
            padding: 1rem 2rem;
            background-color: $primary;
            color: white;
            transition: all ease-in-out 300ms;
            &:hover {
                background-color: white;
                color: black;
                border: 1px solid black;
            }
        }
    }
}